import React from "react";
import FooterLink from "../FooterLink/FooterLink";
import Header from "../Header/Header";

import skillsVector from './../../assets/skills_vector.png';
import { skillList } from './../../assets/skillsData'
import SkillCard from "./SkillCard";

const Skills = () => {
    return(
        <div className = "section-container">
            <Header 
                heading = "My Skills"
                details = "Passionate about new technologis, keep exploring stuff. Here's teck stack I've worked with."
            />


            <div className="skill-card-container">
                {
                    skillList.map(({skillName, skillUrl}) => {
                        return <SkillCard skillName = {skillName} skillUrl = {skillUrl}/> 
                    })
                }
            </div>


            <FooterLink 
                phrase = "Get in " link = "touch. " toAddress = "/contact"
            />

            <div className="skills-vector-frame">
                <img src={skillsVector} alt="skills" className="skills-vector"/>
            </div>

        </div>
    )
}

export default Skills;